.ViewList {
	display: flex;
	margin: 10px;
	width: 100%;
	padding: 0;
}

.ViewListformBox {
	flex-wrap: wrap;
	display: flex;
	border: 1px solid #ced4da;
	border-radius: .25rem;
	padding: 5px;
	min-height: 100px;
	margin-bottom: 10px;
}

.ViewList .Viewitle {
	text-align: center;
	padding: 7px;
	font-size: 14px;
	width: 150px;
	height: 38px;
	color: #555;
	border: solid 1px #ced4da;
	border-radius: .25rem 0 0 0.25rem;
}

.ViewList .ViewContent {
	font-size: 14px;
	padding: 7px;
	width: calc(100% - 150px);
	border-top: solid 1px #ced4da;
	border-right: solid 1px #ced4da;
	border-bottom: solid 1px #ced4da;

	color: #555;
	background: #fafafa;
	border-radius: 0 .25rem .25rem 0;
	height: 38px;
}

.editorBox {
	border: 1px solid #ced4da !important;
	padding: 15px !important;
	border-radius: .25rem;
	height: 500px !important;
	margin-bottom: 20px;
}
.editorToolbar {
	border: 1px solid #ced4da !important;
	border-radius: .25rem;
}

.editorWrapper {
	margin: 0 auto;
	width: 100%;
}

.Table_main {
	text-align: start;
}
.Table_main tbody {
	background: #fff;
}

.react-bootstrap-table-pagination .dropdown .btn {
	font-size: 14px;
	width: 60px;
	border: 0px;
	border-radius: 0;
	height: 32px;
}

.react-bootstrap-table-pagination div {
	text-align: left;
}

.react-bootstrap-table-pagination .dropdown-item {
	text-align: left;
	font-size: 13px;
}

.react-bootstrap-table-pagination-total {
	margin-left: 10px;
	font-size: 13px;
}

.TableLinkButton {
	border: 0px;
	font-size: 0.8rem;
	padding: 4px;
}

.TableButtonSet {
	display: flex;
	justify-content: flex-start;
}

.TableButtonGroup {
	display: flex;
	justify-content: flex-start;
}

.THeader {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
}

.ToggleList {
	margin-top: 15px;
}

.ToggleListBtn {
	border-radius: 0;
	height: 30px;
	padding: 4px 10px;
	margin-right: 5px;
	background: #fff !important;
	border: 1px solid #dbdde2 !important;
	color: #666;
	font-size: 12px;
	cursor: pointer;
}
.ToggleListBtn:hover {
	color: #474d57 !important;
	background: #efefef !important;
}

.ToggleListBtn.active {
	background: #9197a0 !important;
}

.TButton {
	min-width: 90px;
	border-radius: 0 !important;
	height: 30px !important;
	padding: 4px 15px !important;
	margin-right: 10px;
	background: #fff;
	border: 1px solid #dbdde2 !important;
	color: #666;
	font-size: 12px !important;
	cursor: pointer !important;
}
.TButton:hover {
	color: #474d57;
	background: #efefef;
	border: 1px solid #666;
}

.TableButtonSet .active {
	background: #9197a0 !important;
	color: #fff !important;
}

.TableButtonSet .Split {
	background-color: #dbdde2;
	height: 20px;
	margin-top: 5px;
	margin-left: 5px;
	margin-right: 15px;
	width: 2px;
	font-size: 0;
	border: 1px solid #dbdde2;
}

.TableImage:hover {
	cursor: pointer;
}

.TopSearchBar .input-group {
	margin-top: 0;
}

.TopSearchBar .ClearBT {
	margin-top: 0;
	background: #fff;
	border: 1px solid #dbdde2;
	border-radius: 0;
	height: 30px;
	color: #666;
	font-size: 12px;
}

.TopSearchBar .SearchForm {
	background: #fff;
	border: 1px solid #dbdde2;
	border-radius: 0;
	height: 30px;
	color: #666;
	font-size: 12px;
}

.TopSearchBar .SearchBT {
	background: #9197a0;
	border: 1px solid #9197a0;
	border-radius: 0;
	height: 30px;
	color: #fff;
	font-size: 12px;
}

.TopSearchBar .ClearBT:hover,
.TopSearchBar .SearchBT:hover {
	color: #474d57;
	background: #efefef;
	border: 1px solid #dbdde2;
}

.BT_jarr {
	background: #fff;
	border: 1px solid #238b23;
	border-radius: 5;
	width: 60px;
	height: 20px;
	float: left;
	margin: 0 10px 0px 0;
	color: #238b23;
}
.BT_jarr:hover {
	cursor: pointer;
	background-color: #238b23;
	color: #fff;
}

.BT_GoStore {
	background: #fff;
	border: 1px solid #238b23;
	border-radius: 5;
	width: 40px;
	height: 20px;
	color: #238b23;
}
.BT_GoStore:hover {
	cursor: pointer;
	background-color: #238b23;
	color: #fff;
}

.table td,
.table th {
	color: #555;
	font-size: 13px;
	font-weight: 400;
	max-width: 500px;
	line-height: unset;
	overflow: hidden;
}

.table thead th {
	position: relative;
	font-weight: 500;
	background: #edf0f5;
	border: 1px solid #d3d8e1;
	padding: 9px 5px;
}

.selection-cell {
	width: 25px;
}

.PImage {
	margin: 0 5px 0 0;
	width: 20px;
	height: 20px;
}

.imageHoverView {
	position: absolute;
	z-index: 99;
	top: 0;
	right: 0;
	width: 300px;
	height: 300px;
	background: #3e8f3e;
}

.imageHoverViewOld {
	position: absolute;
	z-index: 99;
	top: 200px;
	left: 50%;
	transform: translateX(-50%);
	width: 800px;
	height: 800px;
	background: #3e8f3e;
}

.page-item .page-link {
	font-size: 13px;
	border-radius: 0;
	color: #555;
	background-color: #fff;
	border-color: #dbdde2;
}

.page-item.active .page-link {
	z-index: 1;
	font-size: 13px;
	border-radius: 0;
	color: #3e8f3e;
	background-color: #fff;
	border-color: #3e8f3e;
}

.Caret {
	padding-left: 2px;
	font-size: 15px;
}

.btn:focus {
	outline: none !important;
	box-shadow: none !important;
}
